import BaseTabbar from '@/components/base/BaseTabbar.vue';
import $uni from '@/utils/uni/api.js';

export default {
	data() {
		return {
			elements: [
				{
					title: '图片主色调',
					name: 'mainColor',
					color: 'cyan',
					icon: 'newsfill',
					url: '/pages/index/MainColor'
				},
				{
					title: '字符图',
					name: 'ascii',
					color: 'blue',
					icon: 'colorlens',
					url: '/pages/index/Ascii'
				}
			]
		};
	},

	onLoad() {},

	methods: {
		redirect(e) {
			const { url } = e.currentTarget.dataset;
			
			$uni.route.navigateTo(url);
		}
	},

	components: {
		BaseTabbar
	}
};
